<template>
  <div class="map">
    <div id="mapContainer"></div>
    <el-dialog
      title="弹窗标题"
      :visible.sync="dialogVisible"
      :before-close="handleClose"
    >
      <p>{{ dialogText }}</p>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false,
      markersInfo: [
        { lng: 116.418, lat: 39.921, content: "这是位置1" },
        { lng: 116.407, lat: 39.923, content: "这是位置2" },
        { lng: 116.415, lat: 39.915, content: "这是位置3" },
      ],
      dialogText: "",
    };
  },
  mounted() {
    const map = new window.BMap.Map("mapContainer");
    const point = new window.BMap.Point(116.404, 39.915);
    map.centerAndZoom(point, 15);

    this.markersInfo.forEach(({ lng, lat, content }) => {
      const marker = new window.BMap.Marker(new window.BMap.Point(lng, lat));
      map.addOverlay(marker);

      marker.addEventListener("click", () => {
        this.dialogText = content;
        this.dialogVisible = true;
      });
    });
  },
  methods: {
    handleClose(done) {
      this.dialogVisible = false;
      done();
    },
  },
};
</script>

<style lang="scss" scoped>
.map {
  width: 100%;
  height: 100%;
}

#mapContainer {
  width: 100%;
  height: 100%;
}
</style>
